define('dummy/controllers/demos/sl-span', ['exports', 'ember'], function (exports, Ember) {

    'use strict';

    exports['default'] = Ember['default'].Controller.extend({
        init: function init() {
            this._super();

            var self = this;

            setTimeout(function () {
                self.set("isLoading", false);
            }, 5000);
        },

        isLoading: true
    });

});